/* 
for icon */
.contact-my {
  background: url(/First-ReactApp/static/media/contact.00998a63.jpg);
  background-size: cover;
}

.social-links {
  display: -webkit-flex;
  display: flex;
}

.social-links a {
  text-align: center;
  text-decoration: none;
  color: #000;

  margin: 0 30px;

  position: relative;
  overflow: hidden;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.social-links a .fab {
  font-size: 30px;
  line-height: 80px;
  position: relative;
  z-index: 10;
  transition: color 0.5s;
}

.social-links a::after {
  content: "";
  width: 100%;
  height: 100%;
  top: -90px;
  left: 0;

  position: absolute;
  transition: 0.5s;
}

.social-links a:hover::after {
  top: 0;
}

.social-links a:hover .fab {
  color: #fff;
}

.social-links a:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
@media only screen and (max-width: 600px) {
  .social-links a .fab {
    font-size: 25px;
    line-height: 80px;
    position: relative;
    z-index: 10;
    transition: color 0.5s;
  }
  .social-links a {
    text-align: center;
    text-decoration: none;
    color: white;

    margin-right: 10px;

    position: relative;
    overflow: hidden;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
  }
  .contact-my {
    color: #fff;
  }
}

